<template>
  <Width100 classnames="about_me_container" :addPadding="true">
    <Width50>
      <Width100 :addPadding="true">
        <div class="me_img">
          <VueImage :src="mai" />
        </div>
      </Width100>
      <Width100>
        <div class="view_resume_container">
          <ResumeLink />
        </div>
      </Width100>
    </Width50>
    <Width50>
      <div class="me-paragraph">
        <Paragraph v-for="(info, idx) in aboutMe"
          v-bind:key="idx"
          :paragraph="info"
        />
      </div>
    </Width50>
  </Width100>
</template>

<script>
import AnchorButton from '../common/AnchorButton';
import VueImage from '../common/VueImage';
import Paragraph from '../common/Paragraph';
import ResumeLink from '../common/ResumeLink';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import mai from "../../assets/img/mai.png";
import ign_header from "../../assets/img/ign_header.png";

export default {
    name: 'AboutMe',
    components: {
      AnchorButton,
      Paragraph,
      Width50,
      Width100,
      VueImage,
      ResumeLink,
    },
    mounted () {
      this.$ga.page({
        page: '/about_me',
        title: 'About Me',
        location: window.location.href
      });
    },
    data() {
      return {
        mai,
        aboutMe: [
"Hello! I am a multidisciplinary UX designer specializing in user interface and graphic design. I am currently a freelance designer creating engaging digital experience and beautiful illustrations.",
"Using extensive user research and business analysis, I help develop deeper knowledge to clients and users. I am proficient in Sketch, Adobe Photoshop, AfterEffects, Invision and have been studying design fundamentals such as information architecture, competitor research, typography, and prototyping in my curriculum at CareerFoundry.",
"I have a great passion for minimal design paired with modern aesthetics. Working in the technological and entertainment industry for 3 years, I thrive in a high collaborative environment. Whether I create UI’s, logos, or design websites, I look forward to contributing my analysis and design talent to impact your business’s goals.",
        ],
      }
    }
};
</script>

<style lang="scss" scoped>
.about_me_container {
  height: 100vh;
  margin-top: 32px;

  .paragraph-container {
    margin-bottom: 16px;
  }
}

.view_resume_container {
  text-align: center;
  margin: 32px auto 0;
}

.me_img {
  margin: 0 auto;
  width: 300px;
  height: 300px;
}
.me-paragraph {
  line-height: 2;
}

@media screen and (max-width: 900px) {
  .about_me_container {
    height: 100%;
    padding: 32px;
  }

  .me_img {
    margin: 0 auto;
    width: 80%;
    height: 80%;
  }
  .view_resume_container {
    margin-bottom: 16px;
  }
}

</style>



